<template>
  <section class="ui-upload">
    <b class="iconfont icon-add"></b>
    <input ref="file" type="file" accept="image/jpeg, image/png, image/gif" :multiple="multiple"
           @change="handChange"/>
  </section>
</template>

<script>
  export default {
    name: "file-image",
    props: {
      multiple: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {}
    },
    methods: {
      handChange(event) {
        console.log(event)
        this.$emit('change', event)
        if (!event.target.files.length) {
          return
        }
        for (let i = 0; i < event.target.files.length; i++) {
          let fr = new FileReader()
          fr.onload = event => {
            console.log(event)
            this.$emit('load', event, i)
          }
          fr.readAsDataURL(event.target.files[i])
        }
        this.$refs.file.value = ''
      },
    }
  }
</script>

<style lang="scss" scoped>
  .ui-upload {
    text-align: center;
    color: #e5e5e5;
    border: solid #e5e5e5 1px;
    position: relative;
    overflow: hidden;
    .iconfont {
      line-height: 26.4vw;
      font-size: 30px;
    }
    input[type="file"] {
      width: 100%;
      z-index: 9;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0;
    }
  }
</style>



